import { useEffect, useState } from 'react';
import { useAuth } from '../../../hooks/userHook';
import {
  WrappNav,
  TitleHomeNav,
  TitleTeacherNav,
  TitleFavoriteNav,
} from './nav.styled';
import { useLocation } from 'react-router-dom';

const Navigation = ({ handleNavLinkClick }) => {
  const { isLoggIn } = useAuth();

  //  url
  const location = useLocation();

  //  ,   url   localStorage.getItem('currentPath').   === null,  location.pathname
  const [, setCurrentPath] = useState(() => {
    return localStorage.getItem('currentPath') || location.pathname;
  });

  // currentPath      
  const updateCurrentPath = path => {
    setCurrentPath(path);
    localStorage.setItem('currentPath', path);
  };

  useEffect(() => {
    const handleLocationChange = () => {
      updateCurrentPath(location.pathname);
    };

    //       
    window.addEventListener('popstate', handleLocationChange);

    //  
    return () => {
      window.removeEventListener('popstate', handleLocationChange);
    };
  }, [location.pathname]);

  return (
    <nav>
      <WrappNav>
        <li>
          <TitleHomeNav
            to="/"
            onClick={() => handleNavLinkClick && handleNavLinkClick('/')}
          >
            Home
          </TitleHomeNav>
        </li>
        <li>
          <TitleTeacherNav
            to="/teachers"
            onClick={() =>
              handleNavLinkClick && handleNavLinkClick('/teachers')
            }
          >
            Teachers
          </TitleTeacherNav>
        </li>
        {isLoggIn && (
          <li>
            <TitleFavoriteNav
              to="/favorite"
              onClick={() =>
                handleNavLinkClick && handleNavLinkClick('favorites')
              }
            >
              Favorite
            </TitleFavoriteNav>
          </li>
        )}
      </WrappNav>
    </nav>
  );
};

export default Navigation;
