import { useAuth } from '../../hooks/userHook';
import UserMenu from '../useMenu/userMenu';
import UserNavButton from './button/userNavButton';
import Logo from './logo/logo';
import Navigation from './navigation/nav';
import {
  HeaderContainer,
  WrappContainer,
  BurgerMenu,
  BurgerMenuContainer,
  CloseButton,
  WrappNav,
  WrappUserMenu,
} from './header.styled';
import { RxHamburgerMenu } from 'react-icons/rx';
import React, { useEffect, useState } from 'react';
import { CloseSvg } from '../image/close';
import { useNavigate } from 'react-router-dom';

const Header = () => {
  const { isLoggIn } = useAuth();

  //   
  const navigate = useNavigate();

  // state    burger menu
  const [menuOpen, setMenuOpen] = useState(false);

  //   
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

  const toggleMenu = () => {
    setMenuOpen(!menuOpen);
  };

  useEffect(() => {
    //       ,       600,   ,  ,  '
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
      if (window.innerWidth > 600 && menuOpen) {
        setMenuOpen(false); //  ,    600px
      }
    };
    //  
    window.addEventListener('resize', handleResize);

    //  .     
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, [menuOpen]);

  //            
  const handleNavLinkClick = path => {
    console.log(handleNavLinkClick);
    navigate(path);
    setMenuOpen(false);
  };

  return (
    <HeaderContainer>
      <Logo />

      <WrappContainer>
        <BurgerMenu>
          <RxHamburgerMenu onClick={toggleMenu} />
        </BurgerMenu>
        {menuOpen && (
          <BurgerMenuContainer $isOpen={menuOpen}>
            <CloseButton onClick={() => setMenuOpen(false)}>
              <CloseSvg />
            </CloseButton>

            <WrappNav>
              <Navigation handleNavLinkClick={handleNavLinkClick} />
            </WrappNav>

            <WrappUserMenu>
              {!isLoggIn ? <UserNavButton /> : <UserMenu />}
            </WrappUserMenu>
          </BurgerMenuContainer>
        )}
      </WrappContainer>

      {!menuOpen && windowWidth > 600 && <Navigation />}
      {!menuOpen && windowWidth > 600 && (
        <>{!isLoggIn ? <UserNavButton /> : <UserMenu />}</>
      )}
    </HeaderContainer>
  );
};

export default Header;
